import React from "react";
import moment from "moment";
import { Button, DatePicker, Drawer, Form, Input, message, Select, Space } from "antd";
import { MinusCircleOutlined, PlusOutlined } from "@ant-design/icons";

import apis from "apis";

import { apps, developers, documentsTypes, taskStatus } from "../constants";

const FormItem = Form.Item;
const { Option } = Select;

type AddTaskProps = {
  visible: boolean;
  setVisible: (params: boolean) => void;
  success: () => void;
};
const AddTask = (props: AddTaskProps) => {
  const { visible, setVisible, success } = props;

  const [formInstance] = Form.useForm();

  const submitForm = async () => {
    const values = await formInstance.getFieldsValue(true);

    const {
      preReviewDate = "",
      reviewDate = "",
      technicalReviewDate = "",
      testDate = "",
      publishDate = ""
    } = values;
    const formatMode = "YYYY-MM-DD";
    const coverData = {
      preReviewDate: preReviewDate ? preReviewDate.format(formatMode) : "",
      reviewDate: reviewDate ? reviewDate.format(formatMode) : "",
      technicalReviewDate: technicalReviewDate ? technicalReviewDate.format(formatMode) : "",
      testDate: testDate ? testDate.format(formatMode) : "",
      publishDate: publishDate ? publishDate.format(formatMode) : ""
    };

    const res = await apis.createDemand({ ...values, ...coverData });

    if (res.code === 0) {
      message.success("");
      setVisible(false);
      success?.();
    }
  };

  return (
    <Drawer
      maskClosable
      title=""
      placement="left"
      width="50%"
      open={visible}
      closable={false}
      onClose={() => setVisible(false)}
      extra={
        <Space>
          <Button onClick={() => setVisible(false)}></Button>
          <Button type="primary" onClick={submitForm}>
            
          </Button>
        </Space>
      }
    >
      <Form layout="vertical" form={formInstance}>
        <FormItem label="" name="status">
          <Select placeholder="">
            {taskStatus.map((item) => (
              <Option key={item.name} value={item.status}>
                {item.name}
              </Option>
            ))}
          </Select>
        </FormItem>
        <FormItem label="" name="longMaoDemand">
          <Input placeholder="" />
        </FormItem>
        <FormItem label="">
          <Form.List name="demandDocuments">
            {(fields, { add, remove }) => (
              <>
                {fields.map(({ key, name, ...restField }) => (
                  <Space.Compact block key={key}>
                    <Form.Item
                      {...restField}
                      name={[name, "name"]}
                      rules={[{ required: true, message: "" }]}
                      style={{ width: "100%" }}
                    >
                      <Input placeholder="..." style={{ width: "100%" }} />
                    </Form.Item>
                    <Form.Item
                      {...restField}
                      name={[name, "url"]}
                      rules={[{ required: true, message: "" }]}
                      style={{ width: "100%" }}
                    >
                      <Input placeholder="CF..." style={{ width: "100%" }} />
                    </Form.Item>

                    <Form.Item {...restField} name={[name, "type"]} style={{ width: "100%" }}>
                      <Select placeholder="">
                        {documentsTypes.map((item) => (
                          <Option key={item.label} value={item.value}>
                            {item.label}
                          </Option>
                        ))}
                      </Select>
                    </Form.Item>

                    <Button
                      type="primary"
                      danger
                      onClick={() => remove(name)}
                      icon={<MinusCircleOutlined />}
                    >
                      
                    </Button>
                  </Space.Compact>
                ))}
                <Button type="dashed" onClick={() => add()} block icon={<PlusOutlined />}>
                  
                </Button>
              </>
            )}
          </Form.List>
        </FormItem>

        <FormItem label="">
          <Form.List name="relativeDocuments">
            {(fields, { add, remove }) => (
              <>
                {fields.map(({ key, name, ...restField }) => (
                  <Space.Compact block key={key}>
                    <Form.Item
                      {...restField}
                      name={[name, "name"]}
                      rules={[{ required: true, message: "" }]}
                      style={{ width: "100%" }}
                    >
                      <Input placeholder="..." style={{ width: "100%" }} />
                    </Form.Item>
                    <Form.Item
                      {...restField}
                      name={[name, "url"]}
                      rules={[{ required: true, message: "" }]}
                      style={{ width: "100%" }}
                    >
                      <Input placeholder="..." style={{ width: "100%" }} />
                    </Form.Item>
                    <Form.Item {...restField} name={[name, "type"]} style={{ width: "100%" }}>
                      <Select placeholder="">
                        {documentsTypes.map((item) => (
                          <Option key={item.label} value={item.value}>
                            {item.label}
                          </Option>
                        ))}
                      </Select>
                    </Form.Item>

                    <Button
                      type="primary"
                      danger
                      onClick={() => remove(name)}
                      icon={<MinusCircleOutlined />}
                    >
                      
                    </Button>
                  </Space.Compact>
                ))}
                <Button type="dashed" onClick={() => add()} block icon={<PlusOutlined />}>
                  
                </Button>
              </>
            )}
          </Form.List>
        </FormItem>

        <FormItem name="developers" label="">
          <Select
            mode="multiple"
            allowClear
            style={{ width: "100%" }}
            placeholder=""
            options={developers}
          />
        </FormItem>
        <FormItem name="preReviewDate" label="">
          <DatePicker style={{ width: "100%" }} />
        </FormItem>
        <FormItem name="reviewDate" label="">
          <DatePicker style={{ width: "100%" }} />
        </FormItem>
        <FormItem name="technicalReviewDate" label="">
          <DatePicker style={{ width: "100%" }} />
        </FormItem>
        <FormItem name="testDate" label="">
          <DatePicker style={{ width: "100%" }} />
        </FormItem>
        <FormItem name="publishDate" label="">
          <DatePicker style={{ width: "100%" }} />
        </FormItem>
        <FormItem name="relationalApps" label="">
          <Select
            mode="multiple"
            allowClear
            style={{ width: "100%" }}
            placeholder=""
            options={apps}
          />
        </FormItem>
        <FormItem label="" name="remark">
          <Input.TextArea placeholder="..." />
        </FormItem>
      </Form>
    </Drawer>
  );
};

export default AddTask;
